@import url('https://fonts.googleapis.com/css2?family=Bitter:wght@300&family=Kanit&family=Noto+Serif:ital,wght@1,300&family=Roboto+Condensed:ital,wght@1,300&family=Roboto+Slab&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* Modified branc */
.head h1 {
  font-family: 'PT Sans';
  font-size: 6rem;
  color: #981F48;
}

.head p {
  text-transform: uppercase;
}

nav {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* background-color: beige; */
  margin-bottom: 10px;
  /* border: 2px solid #000000; */
  background-color: #282c34;
  margin-top: 10px;
  cursor: pointer;
}

nav button {
  padding: 1.5% 8%;
  font-weight: bold;
  border: none;
  background-color: #282c34;
  color: white;
  cursor: pointer;
}
.current{
  background-color: #981F48;
  color: #f7f3f3;

}
.nave_btns:hover{
  background-color: #981f47b5;

}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  margin: 10px;
}


.sect1 {
  background-color: #981F48;
  height: 15rem;
  padding: 20px;
  text-align: center;
  color: white;
  font-family: 'Bitter', serif;
  font-family: 'Kanit', sans-serif;
  font-family: 'Noto Serif', serif;
  font-family: 'Roboto Condensed', sans-serif;
  font-family: 'Roboto Slab', serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
}

img {
  height: 600px;
}

.sect1>div {
  display: flex;
  gap: 30px;
  padding: 20px;
}

.sect1>div button {
  padding: 10px 30px;
  background-color: transparent;
  color: beige;
  border: 2px solid white;
}

.sect2 {
  margin: 0 100px;
  text-align: center;
  line-height: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: large;
  font-family: 'Bitter', serif;
  font-family: 'Kanit', sans-serif;
  font-family: 'Noto Serif', serif;
  font-family: 'Roboto Condensed', sans-serif;
  font-family: 'Roboto Slab', serif;
  gap: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.sect2>p {
  text-align: center;
}

.spl {
  color: #981F48;

}

.sec3 {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
  font-size: large;
  font-family: 'Bitter', serif;
  font-family: 'Kanit', sans-serif;
  font-family: 'Noto Serif', serif;
  font-family: 'Roboto Condensed', sans-serif;
  font-family: 'Roboto Slab', serif;
}

.sec3 h1 {
  color: #981F48;
  font-size: 2rem;
 
}

hr {
  margin-top: 10px;
  color: #000000;
  border: 2px solid #000000;
}
.dark_foot{
  text-align: center;
  padding: 20px;
  border: 2px solid #000000;
  color: aliceblue;
  background-color: #282c34;
  margin-bottom: 5px;
}
.dark_foot:hover{
  color: #de2261;
}
.foot p{
 text-align: center;
}
body{
  cursor: pointer;
}